.header {
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
  margin: 24px 0 16px;
  padding: 0 40px;
}
@media (min-width: 768px) {
  .header {
    h1 {
      font-size: 2.57142857rem;
      line-height: 1.33333333;
      font-weight: 600;
      margin-bottom: 2px;
    }
  }
}
